import { loadState } from '@nextcloud/initial-state';
import NcAvatar from '@nextcloud/vue/dist/Components/NcAvatar';
import NcDashboardWidget from '@nextcloud/vue/dist/Components/NcDashboardWidget';
import NcDashboardWidgetItem from '@nextcloud/vue/dist/Components/NcDashboardWidgetItem';
import moment from '@nextcloud/moment';
export default {
  name: 'Dashboard',
  components: {
    NcAvatar: NcAvatar,
    NcDashboardWidget: NcDashboardWidget,
    NcDashboardWidgetItem: NcDashboardWidgetItem
  },
  data: function data() {
    return {
      statuses: [],
      loading: true
    };
  },
  computed: {
    items: function items() {
      var _this = this;
      return this.statuses.map(function (item) {
        var icon = item.icon || '';
        var message = item.message || '';
        if (message === '') {
          if (item.status === 'away') {
            message = t('user_status', 'Away');
          }
          if (item.status === 'dnd') {
            message = t('user_status', 'Do not disturb');
          }
        }
        var status = item.icon !== '' ? "".concat(icon, " ").concat(message) : message;
        var subText;
        if (item.icon === null && message === '' && item.timestamp === null) {
          subText = '';
        } else if (item.icon === null && message === '' && item.timestamp !== null) {
          subText = moment(item.timestamp, 'X').fromNow();
        } else if (item.timestamp !== null) {
          subText = _this.t('user_status', '{status}, {timestamp}', {
            status: status,
            timestamp: moment(item.timestamp, 'X').fromNow()
          }, null, {
            escape: false,
            sanitize: false
          });
        } else {
          subText = status;
        }
        return {
          mainText: item.displayName,
          subText: subText,
          avatarUsername: item.userId
        };
      });
    }
  },
  mounted: function mounted() {
    try {
      this.statuses = loadState('user_status', 'dashboard_data');
      this.loading = false;
    } catch (e) {
      console.error(e);
    }
  }
};